@font-face {
  font-family: 'icons';
  src:  url('../fonts/icons.eot?dpanax');
  src:  url('../fonts/icons.eot?dpanax#iefix') format('embedded-opentype'),
    url('../fonts/icons.ttf?dpanax') format('truetype'),
    url('../fonts/icons.woff?dpanax') format('woff'),
    url('../fonts/icons.svg?dpanax#icons') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="_icon-"]::before,
[class*=" _icon-"]::before {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icons' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

